<template>

    <div class="app">
        <van-list
                v-model="loading"
                :finished="finished"
                finished-text="没有更多了~"
                @load="getList"
                v-if="listData.length"
        >
        <ul  class="moneyList">
            <li v-for="(item,index) in listData">
                <router-link class="ov" :to="{path:'/redchat', query:{groupId:item.id,type:'all',currency:item.currency_id, order:index+1}}">
                    <div class="fl headImg"><img :src="item.currency.icon" alt=""></div>
                    <div class="fl infoBox">
                        <p class="nameBox"><span class="name">{{item.money}}个币{{item.count}}个包</span></p>
                        <p class="time">{{item.send_rule == 1 ? '最小' : '最大'}}的发</p>
                    </div>
                    <div class="fr eosNumber">
                        <p class="number">成员数{{item.user_count}}</p>
                        <p class="less">加入</p>
                    </div>
                </router-link>
            </li>

        </ul>
        </van-list>
        <div v-if="!listData.length" class="searchresult">
            <img src="@/static/images/tab/wujilv_image.png" alt="">
            <p>空空如也</p>
        </div>
    </div>
</template>

<script>
    import { currencyRed } from "@/api/index"
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';
    import { List } from "vant"
    import 'vant/lib/list/style'

    export default {
        name: "Searchred",
        components: {
            [List.name]: List,
        },
        data()
        {
            return {
                loading:false,
                finished:false,
                page:1,
                room_number:'',
                currency_id:'',
                listData:[]
            }
        },
        mounted()
        {
            this.currency_id = this.$route.query.currencyId
            this.getList()
        },
        methods:{

            // 获取列表
            getList()
            {
                this.loading = true
                currencyRed({currency_id:this.currency_id, room_number: this.room_number, page:this.page}).then(res => {
                    this.loading = false
                    this.page++
                    if (res.length < 20)
                    {
                        this.finished = true
                    }
                    this.listData = this.listData.concat(res)
                })
            }
        }
    }
</script>

<style scoped>
    .app .searchBox {
        overflow: hidden;
        padding: 0.1rem 0.15rem;
        background: #f5f5f5;
    }
    .app .searchBox .searchInputBox {
        float: left;
        background: #fff;
        width: 3.45rem;
        border-radius: 0.02rem;
        position: relative;
    }
    .app .searchBox .searchInputBox i {
        background: url("../../static/images/my/btn_search_gray.png");
        background-size: 100%;
        width: 0.16rem;
        height: 0.16rem;
        position: absolute;
        left: 0.11rem;
        top: 0.11rem;
    }
    .app .searchBox .searchInputBox input {
        height: 0.34rem;
        margin-left: 0.32rem;
        float: left;
        width: 80%;
        font-size: 0.14rem;
        color: #8E8E93;
    }
    .app .moneyList li {
        border-bottom: 0.01rem solid #F5F5F5;
    }
    .app .moneyList li a {
        display: block;
        padding: 0.15rem;
    }
    .app .moneyList li a .headImg {
        width: 0.45rem;
        height: 0.45rem;
        /*background: #FF7743;*/
        /*border-radius: 0.02rem;*/
        margin-right: 0.1rem;
    }
    .app .moneyList li a .headImg img {

        width: 100%;
        height: 100%;
        border-radius: 50%;
    }
    .app .moneyList li a .headImg p {
        text-align: center;
        color: #fff;
        font-size: 0.1rem;
        line-height: 0.45rem;
    }
    .app .moneyList li a .infoBox .nameBox .name {
        color: #404040;
        font-size: 0.14rem;
    }
    .app .moneyList li a .infoBox .time {
        font-size: 0.1rem;
        color: #999999;
        margin-top: 0.05rem;
    }
    .app .moneyList li a .eosNumber {
        text-align: right;
    }
    .app .moneyList li a .eosNumber .number {
        font-size: 0.12rem;
        color: #404040;
    }
    .app .moneyList li a .eosNumber .less {
        color: #FFFFFF;
        font-size: 0.1rem;
        padding: 0.01rem 0.06rem;
        background: #FABD69;
        display: inline-block;
        border-radius: 0.03rem;
        margin-top: 0.05rem;
    }

    .searchresult {
        height: 70vh;
        text-align: center;
    }
    .searchresult img {
        width: 1.1rem;
        height: 1rem;
        display: block;
        margin: auto;
        padding-top: 2rem;
    }
    .searchresult p {
        font-size: 0.14rem;
        color: #666666;
        margin-top: 0.2rem;
    }
</style>



